import React, { Component } from 'react';

import SendViaFile from './static/img/SendViaFile.gif';
import CustomTable from './CustomTable';

import { TableCell, TableRow, Typography, Grid } from '@material-ui/core';


class Donate extends Component {
    
    render() {
        const columns = [
            { id: 'logo', numeric: false, disablePadding: true, label: '' },
            { id: 'name', numeric: false, disablePadding: false, label: 'Name' },
            { id: 'message', numeric: false, disablePadding: false, label: 'Message' },
            { id: 'amount', numeric: true, disablePadding: false, label: 'Amount' },
            { id: 'date', numeric: true, disablePadding: false, label: 'Date' },
        ];

        const donors = [
            { logo: "LOGO", name: "Guy Corem", message: "Message", amount: "$1800", date: "07-01-2019"}
        ];

        function buildRow(row, index) {
            return (
                <TableRow
                    hover
                    /*onClick={event => handleClick(row.commitment)}*/
                    role='hover'
                    tabIndex={-1}
                    key={row.name}
                >
                    <TableCell scope="row" padding="none">
                        {row.logo}
                    </TableCell>
                    <TableCell scope="row" padding="none">
                        {row.name}
                    </TableCell>
                    <TableCell>{row.message}</TableCell>
                    <TableCell align="right">{row.amount}</TableCell>
                    <TableCell align="right">{row.date}</TableCell>
                </TableRow>
            );
        }

        return (
            <React.Fragment>
                <section style={{ backgroundColor: '#aaaaaa', padding: '0px 0px 100px 0px' }}>
                    <br /><br />
                    <br /><Typography color="secondary" variant="h3">Supporters</Typography><br />
                    <br />
                    <Grid container spacing={0} justify='center' style={{ width: '90%', margin: '0 auto'}}>
                        <Grid item xs={2}></Grid>
                        <Grid item xs={8}>
                            <CustomTable
                                columns={columns}
                                items={donors}
                                buildRow={buildRow}
                                dense={true}
                                sortable={false}
                            />
                        </Grid>
                        <Grid item xs={2}></Grid>
                    </Grid>
                </section>
            </React.Fragment>
        );
    }
}

export default Donate;